/* Navbar.css */
.Navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 20px;
    height: 100px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 100vw;
    background-color: #fff;
    z-index: 1000;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.logo h1 {
    font-size: 32px;
    color: #333;
    cursor: pointer;
    transition: color 0.3s;
}

.logo h1:hover {
    color: #007bff;
}

.nav ul {
    list-style: none;
    display: flex;
    gap: 24px;
    font-size: 32px;
    ;
}

.nav a {
    text-decoration: none;
    color: #333;
}

.nav a:hover {
    color: #007bff;
    /* Change color on hover */
}